<template>
  <span class="ml-2">
    <tooltip-icon-button
      v-if="enableErrorLogs"
      :btnColor="buttonColor"
      btnIcon="fa-warning"
      buttonSize="small"
      btnTooltip="Error logs"
      :onClick="() => redirectToErrorLogPage()"
    />
  </span>
</template>
<script>
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import * as errorLogService from '../../services/errorLogService.js';

export default {
  name: 'error-logs',
  components: { TooltipIconButton },
  data() {
    return {
      errorlogExist: false,
    };
  },
  computed: {
    enableErrorLogs() {
      return this.$store.state.setting.enableErrorLogs;
    },
    buttonColor() {
      return this.errorlogExist ? 'error' : 'grey';
    },
  },
  watch: {
    enableErrorLogs: function (currentVal, prevVal) {
      if (currentVal !== prevVal) {
        this.fetchErrorLogsExist();
      }
    },
  },
  mounted() {
    this.fetchErrorLogsExist();
  },
  methods: {
    redirectToErrorLogPage() {
      this.$router.push({ path: '/error-logs' });
    },
    fetchErrorLogsExist() {
      errorLogService.exists().then(value => {
        if (value && Object.keys(value).length > 0) {
          this.errorlogExist = true;
        }
      });
    },
  },
};
</script>
